.intro-section{
    background: #FFF;
    padding: 5rem 0 0 0;
    width: 100%;
    height: 100%;
}

.offer-section-items{
    background: #FFFFFF;
    padding: 2rem 0 0 0;
    width: 100%;
    height: 100%;
}

.get-started-section{
    padding: 6.5rem 0 6.5rem 0;
    width: 100%;
    height: 100%;
    border: 1px solid #000;
    background: linear-gradient(180deg, #F18825 0%, #F46565 100%);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.get-started-cta{
    justify-content: center;
}

.cta-container{
    position: relative;
    padding: 10px 20px;
    text-align: center;
    .cta-overlay{
        position: absolute;
        inset: 0px;
    }
    *{
        text-align: center;
        position: relative;
        z-index: 1;
    }
}